@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@200;400;500&family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Raleway:wght@100;200;300;400&family=Quicksand:wght@300;400;500&display=swap");

:root {
  --primary-color: rgb(0, 51, 204);
  --secondary-color: rgb(239, 32, 239);
  --danger-color: rgb(230, 25, 25);
}

html,
body {
  padding: 0;
  margin: 0;
}

.smooth-font {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.text-dark-grey {
  color: rgb(61, 61, 61);
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Montserrat", sans-serif;
}

a {
  text-decoration: none;
}

.self-center {
  align-self: center;
}

.color-red {
  color: red;
}

.box-shadow {
  box-shadow: 0 0 12px rgb(224, 224, 224);
}

.rounded-full {
  border-radius: 100%;
}

.numberFont {
  font-family: "Raleway", "Montserrat", sans-serif;
}

.font-nunito {
  font-family: "Nunito", "Montserrat", "Raleway", sans-serif;
}

.font-quicksand {
  font-family: "Quicksand", "Nunito", "Montserrat", "Raleway", sans-serif;
}

.mx-auto {
  margin: 0 auto;
}

.h-screen {
  height: 100vh;
}

.min-h-screen {
  min-height: 100vh;
}

.heading-1 {
  font-size: 2rem;
}

.heading-2 {
  font-size: 1.5rem;
}

.heading-3 {
  font-size: 1.2rem;
}

.container {
  max-width: 1100px;
}

.border {
  border: 1px solid rgb(227, 221, 221);
}

.border-radius-sm {
  border-radius: 2px;
}

.border-radius-lg {
  border-radius: 6px;
}

.text-left {
  text-align: left;
}

.gap-2 {
  gap: 0.5rem;
}

.gap-4 {
  gap: 1rem;
}

.icon {
  font-size: 20px;
}

.letter-spacing-2 {
  letter-spacing: 0.2rem;
}

.color-green {
  color: rgb(0, 189, 0);
}
.font-lighter {
  font-weight: 300;
}

.font-light {
  font-weight: 400;
}

.font-medium {
  font-weight: 500;
}

.font-bold {
  font-weight: 700;
}

.overflow-hidden {
  overflow: hidden;
}

.font-bolder {
  font-weight: 800;
}

.flex {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

.flex-row {
  flex-direction: row;
}

.gap-2 {
  gap: 2;
}

.flex-wrap {
  flex-wrap: wrap;
}

.flex-1 {
  flex: 1;
}

.p-2 {
  padding: 0.5rem;
}

.p-4 {
  padding: 1rem;
}

.text-center {
  text-align: center;
}

.text-white {
  color: white;
}

.m-2 {
  margin: 0.5rem;
}

.m-4 {
  margin: 1rem;
}

.mx-2 {
  margin: 0.5rem 0;
}

.mx-4 {
  margin: 1rem 0;
}

.text-grey {
  color: rgb(26, 26, 26);
}

.mt-2 {
  margin-top: 0.5rem;
}

.mt-4 {
  margin-top: 1rem;
}

.mt-6 {
  margin-top: 1.5rem;
}

.text-primary {
  color: var(--primary-color);
}

.my-2 {
  margin: 0 0.5rem;
}

.my-4 {
  margin: 0 1rem;
}

.gap-4 {
  gap: 4;
}

.my-auto {
  margin: auto 0;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

.mainDiv {
  transition: ease-in-out;
  transition-duration: 300ms;
}

.rounded-full {
  border-radius: 100%;
  padding: 0.2rem;
}

@media only screen and (max-width: 800px) {
  .heading-1 {
    font-size: 1.6rem;
  }

  .mainDiv {
    transition: all;
    transition-duration: 300ms;
    transform: translateX(100px);
  }

  .heading-2 {
    font-size: 1.3rem;
  }

  .heading-3 {
    font-size: 1rem;
  }
}
